<template>
  <div>
    <d2-container>
      <template slot="header">账单收款管理</template>
      <el-row>
        <el-col :span="12">
          <div class="grid-content-top">
            <p class="key">未付清账单</p>
            <p class="value">{{totalData.auditStatusFailNum||0}}个</p>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content-top">
            <p class="key">未收款金额</p>
            <p class="value">{{totalData.auditStatusFailAmt||0}}元</p>
          </div>
        </el-col>
      </el-row>
      <!---->
      <table-comb
        name="账单列表"
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :search-model="tableMainSearchModel"
        :get-action="$api.energy.billManage.list"
        :get-action-where="getActionWhere"
        :autoFetch="false"
        :remove-action="$api.energy.billManage.delete"
      >
        <!--基础查询-->
        <template slot="baseSearchForm" slot-scope="props">
          <el-select
            v-model="props.form.siteId"
            @change="changeSite"
            filterable
            placeholder="请选择站点"
            style="width: 300px;">
            <el-option
              v-for="item in siteOptions"
              :key="item.siteId"
              :label="item.name"
              :value="item.siteId">
            </el-option>
          </el-select>
          <el-input
            placeholder="账单号/客户名称/联系人/电话"
            prefix-icon="el-icon-search"
            clearable
            v-model="props.form.searchKey"
            style="width: 240px; margin-left: 10px;"
          >
          </el-input>
          <el-button type="success" class="fr ml10" @click="auditBill('02')">导出表格</el-button>
          <el-button type="success" class="fr" @click="auditBill('01')">批量催缴</el-button>
        </template>
        <!--高级查询-->
        <template slot="advancedSearchForm" slot-scope="props">
          <el-row>
            <el-col :span="8">
              <el-form-item label="地点：">
                <el-select
                  v-model="props.form.officeId"
                  @change="changeOffice"
                  placeholder="请选择"
                  filterable
                >
                  <el-option
                    v-for="item in officeOptions"
                    :key="item.id"
                    :label="item.description"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="区域：">
                <el-cascader
                  placeholder="请选择"
                  :props="regionProps"
                  :options="regionOptions"
                  v-model="props.form.regionId"
                  :debounce="400"
                  @change="changeRegion">
                </el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="单元：">
                <el-select
                  v-model="props.form.stationId"
                  @change="changeStation"
                  placeholder="请选择"
                  filterable
                >
                  <el-option
                    v-for="item in stationOptions"
                    :key="item.id"
                    :label="item.staDescription"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="缴费状态：">
                <el-select
                  v-model="props.form.auditStatus"
                  @change="changeStation"
                  placeholder="请选择"
                  filterable
                >
                  <el-option
                    v-for="item in auditStatusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="缴费状态：">
                <el-select
                  v-model="props.form.auditStatus"
                  @change="changeStation"
                  placeholder="请选择"
                  filterable
                >
                  <el-option
                    v-for="item in auditStatusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="应收日期：">
                <el-date-picker
                  style="width:214px"
                  v-model="editDialogCustomer.billStartEndDate"
                  type="daterange"
                  range-separator="~"
                    start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
        </template>
        <!--表格-->
        <template slot="tableColumns">
          <el-table-column type="selection" width="35"></el-table-column>
          <el-table-column prop="corpName" label="操作" width="180px">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="detail(props.row)">查看账单</el-button>
              <el-button type="text" size="mini" @click="openSaveDialog(props.row)">缴费</el-button>
              <el-button type="text" size="mini" @click="openSaveDialog(props.row)">催缴</el-button>
              <el-button type="text" size="mini" @click="openSaveDialog(props.row)">打印</el-button>
            </template>
          </el-table-column>
          <el-table-column width="150px" prop="billNo" label="业务流水号"></el-table-column>
          <el-table-column prop="auditStatusDesc " label="缴费状态"></el-table-column>
          <el-table-column width="150px" prop="officeName" label="地点"></el-table-column>
          <el-table-column prop="levelDesc" label="区域"></el-table-column>
          <el-table-column prop="stationName" label="单元"></el-table-column>
          <el-table-column width="120px" prop="billMonthStr" label="月份"></el-table-column>
          <el-table-column prop="feeTypeDesc" label="费项"></el-table-column>
          <el-table-column prop="receivableAmtStr" label="应收金额"></el-table-column>
          <el-table-column width="120px" prop="receivableDateStr" label="应收日期"></el-table-column>
          <el-table-column prop="corpName" label="客户名称"></el-table-column>
          <el-table-column prop="telephone" label="联系人">
            <template slot-scope="props">
              {{(props.row.corpContacts||'-')+'/'+(props.row.telephone||'-')}}
            </template>
          </el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

    <el-dialog title="缴费" :visible.sync="showSaveDialog" >
      <el-form label-width="100px" :model="editDialogCustomer" :rules="rules" ref="saveForm">
        <el-row>
          <el-col :span="12">
            <el-form-item label="地点：">
              {{editDialogCustomer.officeName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="企业客户：">
              {{editDialogCustomer.corpName}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="区域：">
              {{editDialogCustomer.levelDesc}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系人：">
              {{editDialogCustomer.corpContacts}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row >
          <el-col :span="12">
            <el-form-item label="单元：">
              {{editDialogCustomer.stationName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系人电话：" >
              {{editDialogCustomer.telephone }}
            </el-form-item>
          </el-col>
        </el-row>
        <table class="dialog-table">
          <tr>
            <th>月份</th>
            <th>应收日期</th>
            <th>费项</th>
            <th>应收金额</th>
            <th>未缴金额</th>
          </tr>
          <tr>
            <td>1</td>
            <td>2</td>
            <td>3</td>
            <td>4</td>
            <td>4</td>
          </tr>
        </table>
        <el-form-item label="缴费状态：">
          <template>
            <el-radio
              v-model="editDialogCustomer.auditStatus"
              v-for="item in auditStatusOptions2"
              :key="item.value"
              :label="item.value"
            >{{item.label}}
            </el-radio>
          </template>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="可输入缴费方式或其他转账单号"
            v-model="editDialogCustomer.remark">
          </el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">保 存</el-button>
        <el-button @click="showSaveDialog = false">取 消</el-button>
      </div>
    </el-dialog>

    <el-dialog title="查看账单" :visible.sync="showDetailDialog" class="details-info">
      <el-form label-width="120px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="地点：">
              {{currentBill.officeName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="企业客户：">
              {{currentBill.corpName}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="区域：">
              {{currentBill.levelDesc}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系人：">
              {{currentBill.corpContacts}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row >
          <el-col :span="12">
            <el-form-item label="单元：">
              {{currentBill.stationName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系人电话：" >
              {{currentBill.telephone }}
            </el-form-item>
          </el-col>
        </el-row>
        <table class="dialog-table">
          <tr>
            <th>月份</th>
            <th>应收日期</th>
            <th>费项</th>
            <th>应收金额</th>
            <th>未缴金额</th>
          </tr>
          <tr>
            <td>1</td>
            <td>2</td>
            <td>3</td>
            <td>4</td>
            <td>4</td>
          </tr>
        </table>
        <div class="line"></div>
        <el-row >
          <el-col :span="12">
            <el-form-item label="缴费状态：">
              {{currentBill.stationName}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="支付流水号：" >
              {{currentBill.telephone }}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row >
          <el-col :offset="12" :span="12">
            <el-form-item label="第三方流水号：" >
              {{currentBill.telephone }}
            </el-form-item>
          </el-col>
        </el-row>
        <div class="line"></div>
        <div class="remark-list">
          <div class="remark-list-title">备注记录</div>
          <el-table
            :data="currentBill.remarkVo"
            style="width: 100%">
            <el-table-column
              prop="date"
              label="备注时间"
              width="120">
            </el-table-column>
            <el-table-column
              prop="name"
              label="操作人"
              width="120">
            </el-table-column>
            <el-table-column
              prop="address"
              label="备注内容">
            </el-table-column>
          </el-table>
        </div>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showSaveDialog = false">关 闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";

  //时间格式化
  Date.prototype.format = function (format) {
    var o = {
      "M+": this.getMonth() + 1, //month
      "d+": this.getDate(),    //day
      "h+": this.getHours(),   //hour
      "m+": this.getMinutes(), //minute
      "s+": this.getSeconds(), //second
      "q+": Math.floor((this.getMonth() + 3) / 3),  //quarter
      "S": this.getMilliseconds() //millisecond
    }
    if (/(y+)/.test(format)) format = format.replace(RegExp.$1,
      (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)if (new RegExp("(" + k + ")").test(format))
      format = format.replace(RegExp.$1,
        RegExp.$1.length == 1 ? o[k] :
          ("00" + o[k]).substr(("" + o[k]).length));
    return format;
  };


  export default {
    // 如果需要缓存页面
    // name 字段需要设置为和本页路由 name 字段一致
    name: "BillAdd",
    components: {},
    mixins: [
      listMixin
    ],
    data() {
      return {
        totalData:{
          auditStatusWaitNum:0,
          auditStatusWaitAmt:0
        },
        tableMainSearchModelBase:{
          siteId:'',
          searchKey: ''
        },
        tableMainSearchModel:{
          officeId:'',
          regionId:[],
          stationId:'',
          auditStatus:'',
          billMonth:''

        },
        siteOptions:[],
        officeOptions:[],
        regionOptions:[],
        regionProps:{
          value: 'id',
          label:'name',
          children: 'childLists'
        },
        corpDisabled:true,
        templateTypeOptions:[
          {value: '00', label: '通用'},
          {value: '01', label: '多电价阶段'},
        ],
        auditStatusOptions:[
          {value: '00', label: '待审核'},
          {value: '01', label: '审核通过'},
          {value: '02', label: '审核拒绝'},
        ],
        auditStatusOptions2:[
          {value: '00', label: '未付款'},
          {value: '01', label: '已付款'},
        ],
        stationOptions:[],
        feeTypesOptions:[],
        tableData: [],
        fileList:[],
        activeName:'first',
        currentBillActiveName:'first',
        showSaveDialog:false,
        showImportDialog:false,
        showDetailDialog:false,
        currentBill:{
          remarkVo:[]
        },
        editDialogCustomer:{},
        rules:{
          description: [
            { required: true, message: '费用名称不能为空', trigger: 'blur' }
          ],
          officeId:[
            { required: true, message: '请选择地点', trigger: 'blur' }
          ]
        }
      };
    },
    computed: {
      getActionWhere(){
        return {
          unionId : this.unionId
        }
      },
      unionId() {
        return this.$route.params.unionId
      },
    },
    methods: {
      //打开编辑弹窗
      openSaveDialog(item){
        this.showSaveDialog = true;
        //移除校验结果并重置字段值
        this.$refs.saveForm && this.$refs.saveForm.clearValidate();
        //新增不传item 编辑要传入item
        console.log(item);
        if(item && item.id){
          this.editDialogCustomer = {
            ...item,
            regionId:[item.levelId||'',item.level2Id||''],
            officeOptions:this.officeOptions,
            regionOptions:[],
            stationOptions:[],
            auditStatus:'00',
          }
          //地点，地区，单元初始化
          this.initEditDialogCustomerOptions()
        }else{
          this.editDialogCustomer = {
            officeId:'',
            regionId:[],
            stationId:'',
            id:'',
            corpName:'',
            corpContacts:'',
            billMonth:'',
            telephone:'',
            receivableDate:'',
            officeOptions:this.officeOptions,
            regionOptions:[],
            stationOptions:[],
            remark:'',
            totalUseQuantity:'',
            price:'',
            beginMeterReading:'',
            endMeterReading:'',
            rate:''

          }
        }
      },
      //初始化弹窗内容
      initEditDialogCustomerOptions(){
        let officeId = this.editDialogCustomer.officeId;
        let regionId = this.editDialogCustomer.regionId;
        this.$api.merch.station.regionOptions(officeId).then(res => {
          this.editDialogCustomer.regionOptions = res.data;
        });
        this.$api.merch.station.stationOptions(regionId,officeId).then(res => {
          this.editDialogCustomer.stationOptions = res.data;
        });
      },
      //打开导入弹窗
      auditBill(auditStatus){
        //获取选中项的id
        console.log(this.$refs.tableMain.checkedItems,auditStatus);
      },

      //查看详情
      detail(item){
        console.log(item);
        this.showDetailDialog = true;
        this.currentBill = item;
        this.currentBill.billStartEndDate = `${new Date(item.billStartDate).format('yyyy-MM-dd')} - ${new Date(item.billEndDate).format('yyyy-MM-dd')}`;
      },
      //获取value 获取 lable
      getLabelByValue(list,value){
        console.log(value)
        let arr =  list.filter(item=>{
          return item.value == value;
        });
        if(!value || arr.length==0){
          return ''
        };
        return arr[0].label;
      },
      //保存或者新增费项
      save(){
        //验证
        console.log(this.editDialogCustomer);
        this.$refs.saveForm.validate((valid) => {
          console.log(valid)
          if (valid) {
            let _vo = {...this.editDialogCustomer};

            _vo.billStartDate = _vo.billStartEndDate[0];
            _vo.billEndDate = _vo.billStartEndDate[1];

            delete  _vo.billStartEndDate;
            delete  _vo.officeOptions;
            delete  _vo.regionOptions;
            delete  _vo.regionId;
            delete  _vo.stationOptions;


            this.$api.energy.billManage.save(_vo).then(res => {
              if(res.code==0){
                let message = '修改成功！'
                this.showSaveDialog = false;
                this.$message({
                  message,
                  type: 'success'
                });
                //更新列表
                this.$refs.tableMain.fetchData()
              }else{
                this.$message.error(res.message);
              }
            });
          } else {
            return false;
          }
        });
      },
      //切换站点
      changeSite(siteId){

        //获取账单审核状态统计
        this.$api.energy.billManage.total(siteId).then(res=>{
          console.log(res.data)
          this.totalData =  res.data;
        });

        this.$api.merch.region.officeOptions(siteId).then(res => {
          this.officeOptions  = res.data;
          //清空查询条件后面的级联 地点，区域，单元
          let searchModelData = this.$refs.tableMain.searchModelData;
          searchModelData.officeId='';
          searchModelData.regionId=[];
          searchModelData.stationId='';
          this.regionOptions=[];
          this.stationOptions=[];
          //刷新列表
          this.$refs.tableMain.fetchData();

        });
      },
      //切换地点
      changeOffice(officeId){
        this.$api.merch.station.regionOptions(officeId).then(res => {
          this.regionOptions = res.data;
          //清空查询条件后面的级联 区域，单元
          let searchModelData = this.$refs.tableMain.searchModelData;
          searchModelData.regionId=[];
          searchModelData.stationId='';
          this.stationOptions=[];
          //刷新列表
          this.$refs.tableMain.fetchData();

        });
      },
      //切换区域
      changeRegion(regionIdArray){
        let officeId = this.$refs.tableMain.searchModelData.officeId
        this.$api.merch.station.stationOptions(regionIdArray,officeId).then(res => {
          this.stationOptions = res.data;
          //清空查询条件后面的级联 区域，单元
          this.tableMainSearchModel.stationId='';
          //刷新列表
          this.$refs.tableMain.fetchData();
        });
      },
      //切换单元
      changeStation(stationId){
        this.$refs.tableMain.fetchData();
      },
      //新增或者编辑里面的级联
      changeOfficeForSave(officeId,callback){
        this.$api.merch.station.regionOptions(officeId).then(res => {
          let editDialogCustomer = this.editDialogCustomer;
          editDialogCustomer.regionOptions = res.data;
          //清空查询条件后面的级联 区域，单元
          editDialogCustomer.regionId=[];
          editDialogCustomer.stationId='';
          editDialogCustomer.stationOptions=[];
          if(callback){
            callback()
          }
        });
      },
      changeRegionForSave(regionIdArray){
        let officeId = this.editDialogCustomer.officeId
        this.$api.merch.station.stationOptions(regionIdArray,officeId).then(res => {
          let editDialogCustomer = this.editDialogCustomer;
          editDialogCustomer.stationOptions = res.data;
          //清空查询条件后面的级联 区域，单元
          editDialogCustomer.stationId='';
        });
      },
      changeStationForSave(stationId){
        let siteId = this.editDialogCustomer.siteId;
        //获取企业信息
        this.$api.tet.stationCorp.getStationCurrCorpInfo(stationId).then(res=>{
          this.editDialogCustomer.corpName = res.data.corpName;
          this.editDialogCustomer.corpId = res.data.corpName;
          this.editDialogCustomer.corpContacts = res.data.corpContacts;
          this.editDialogCustomer.telephone = res.data.telephone;
        })

      },
      //新增或者编辑里面的级联 end
    },
    mounted() {
      //获取站点列表
      setTimeout(()=>{
          this.$api.merch.region.siteOptions(this.unionId).then(res => {
            let siteId = res.data[0].siteId
            this.$refs.tableMain.searchModelDataBase.siteId = siteId
            this.siteOptions = res.data;
            this.changeSite(siteId);
          });

          //获取费项列表
          this.$api.tet.feeType.getFeeTypesAndAuditStatus().then(res =>{
            console.log(res)
            this.feeTypesOptions = res.data;
          })
      },0)
    }
  };
</script>


<style type="text/scss" lang="scss" scoped>
  .el-tag{ margin-right: 10px; cursor: pointer;}
  .el-dialog__body{ padding: 0 20px;}

  .grid-content-top{
     text-align: center;
    margin-bottom: 20px;
    .key{
      font-size: 14px;
      margin: 0;
      color:#aaa;
    }
    .value{
      font-size: 24px;
      font-weight: 700;
    }
  }
  .line{ width: 100%; height: 1px;background: #ddd; overflow: hidden; margin:0 0 20px 0}
  .el-upload__tip{ color: #E6A23C}

  .details-info .el-form-item{margin-bottom: 0;}
  .dialog-table{ width: 100%; text-align: center; border-collapse: collapse; border: none; line-height: 35px;}
  .dialog-table th{ background: #ddd}
  .remark-list-title{ font-size:16px; padding: 0 0 20px 0}
  .dialog-table{
    text-align:center;
    border-collapse: collapse;
    border:#bfdcf9 solid 1px;
    line-height: 32px;
    margin-bottom: 15px;
    th{ background: #ddeeff;border:#bfdcf9 solid 1px;width: 120px;}
    td{ border:#bfdcf9 solid 1px;}
  }
</style>
