import axios from 'axios'

export default {
  /**
   * 查询收支明细列表数据
   * @returns {*}
   */
  list (condition, paging) {
    paging = Object.assign({
      currentPage: 1,
      pageSize: 10
    }, paging)

    if(condition.startEndTime && condition.startEndTime.length==2){
      condition.dataStartTime = condition.startEndTime[0];
      condition.dataEndTime = condition.startEndTime[1];
      delete condition.startEndTime;
    }
    return axios.post('/energy/payRec/queryPayRecByPage.json', {
      ...condition,
      ...paging
    })
  },
  /**
   * 详情
   */
  detail (payBillRelId) {
    return axios.get('/energy/payRec/queryPayRecDetail.json', {
      params: {
        payBillRelId
      }
    })
  },
  /**
   * Excel导出 电表用量
   * @returns {*}
   */
  exportExcel (vo) {
    //json 转 url
    function setUrlK(ojson) {
      var s='',name, key;
      for(var p in ojson) {
        if(!ojson[p]) {return null;}
        if(ojson.hasOwnProperty(p)) { name = p };
        key = ojson[p];
        s += "&" + name + "=" + encodeURIComponent(key);
      };
      return s.substring(1,s.length);
    };
    var pargam = setUrlK(vo);
    window.open(`/energy/payRec/exportExcel.json?${pargam}`,'_blank');
  },
}
