import axios from 'axios'

export default {
  /**
   * 查询电表用量列表数据
   * @returns {*}
   */
  list (condition, paging) {
    paging = Object.assign({
      currentPage: 1,
      pageSize: 10
    }, paging)
    //修改参数
    if(condition.regionId){
      if(condition.regionId.length>=1){
        condition.levelId = condition.regionId[0];
      }
      if(condition.regionId.length>=2){
        condition.level2Id = condition.regionId[1];
      }
      delete  condition.regionId;
    }

    return axios.post('/energy/billManage/queryBillByPage.json', {
      ...condition,
      ...paging
    })
  },
  /**
   * 根据站点账单审核状态统计
   */
  total (siteId) {
    return axios.get('/energy/billManage/countBillAuditData.json', {
      params: {
        siteId
      }
    })
  },
  /**
   * Excel导出 电表用量
   * @returns {*}
   */
  delete (id) {
    return axios.post('/energy/billManage/delBillById.json', {
      params: {
        id
      }
    })
  },
  /**
   * 保存能耗账单信息
   * @returns {*}
   */
  save(vo) {
    return axios.post('/energy/billManage/saveOrUpdateBill.json', vo)
  },
  /**
   * 下载模板
   * @returns {*}
   */
  getBillTemplate (templateType) {
    window.open(`/energy/billManage/getBillTemplate.json?templateType=${templateType}`,'_blank');
  },
  /**
   * 上传模板
   * @returns {*}
   */
  batchImport (vo) {
    return axios.post('/energy/billManage/batchImport.json', vo)
  },
  /**
   * 能耗账单审核
   * @returns {*}
   */
  auditBill (vo) {
    return axios.post('/energy/billManage/auditBill.json', vo)
  },
}
